import React from "react";
import TextField from "@material-ui/core/TextField";

import Button from '@material-ui/core/Button';




export const renderEditableCell = (value, tableMeta, updateValue, rows) => {



  return (
    <div>
      <TextField
        style={{ width: "32%" }}
        id="standard-basic"
        defaultValue={value}
        onChange={(e) => {
          updateValue(e.target.value);
          tableMeta.rowData[1] = e.target.value;
          rows[tableMeta.rowIndex].count = e.target.value;
        }}
      />
    </div>
  );
};
export const columns = (handleEdit) => [
  {
    name: "NO",
    label: "##",
    options: {
      filter: false,
      sort: true,
    }
  },
  {
    name: "curso",
    label: "Curso",
    options: {
      filter: true,
      sort: true
    }
  },
  {
    name: "turma",
    label: "Turma",
    options: {
      filter: false,
      sort: true,
    }
  },
  {
    name: 'disciplina',
    label: 'Cadeira',
    options: {
      filter: true,
      sort: false
    }
  },
  {
    name: 'docente',
    label: 'Professor',
    options: {
      filter: true,
      sort: false
    }
  },
  {
    name: "semestre",
    label: "Semestre",
  },
  {
    name: "anoLectivo",
    label: "Ano Lectivo",
    options: {
      filter: true,
      sort: true
    }

  },
  {
    name: 'Ações',
    options: {
      filter: false,
      sort: false,
      empty: true,
      customBodyRender: (value, tableMeta, updateValue) => {
        const rowIndex = tableMeta.rowIndex;
        const rowId = tableMeta.tableData[rowIndex].id;

        return (
          <Button onClick={() => handleEdit(rowId)}>Editar</Button>
        );
      }
    }
  }

];


