import MUIDataTable from "mui-datatables";
import React, { useState, useEffect } from "react";
import { columns, errorCols, errorOptions } from "./data_header_table";
import { createMuiTheme, MuiThemeProvider } from "@material-ui/core/styles";
import { requesicoes } from "../../../utils/services";
import { NavLink } from "react-router-dom";
import { Fab } from "@material-ui/core";
import { useHistory } from 'react-router-dom';
//import { useHistory } from "react-router-dom/cjs/react-router-dom.min";
import CryptoJS from 'crypto-js';
import { Autocomplete, Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Grid, Input, MenuItem, TextField } from "@mui/material";
import { ErrorMessage, Form, Formik } from "formik";
import Swal from "sweetalert2";
import * as Yup from 'yup'
import { prototype } from "events";
import { MoonLoader } from 'react-spinners'; // Importe o componente BounceLoader




let turmas = []
let cursos = []
let turmasCadeira = []
let anos = []
export default function MuiDatatableExample() {

  const history = useHistory();
  const [selectedData, setSelectedData] = useState(null);
  const [lastClickTime, setLastClickTime] = useState(0);
  const [open, setOpen] = React.useState(false);
  const [carregando, setCarregando] = useState(true);



  const encryptParam = (param) => {
    const encryptedParam = CryptoJS.AES.encrypt(param.toString(), secretKey).toString();
    return encodeURIComponent(encryptedParam);
  };

  const getMuiTheme = () =>
    createMuiTheme({
      overrides: {
        MUIDataTableHeadCell: {
          fixedHeader: { backgroundColor: "darkblue", color: "white" }
        },
        MUIDataTableSelectCell: {
          headerCell: { backgroundColor: "darkblue" },
          checkboxRoot: { color: "white", filter: "brightness(0.5)" },
          expandDisabled: {
            // Soft hide the button.
            visibility: "hidden"
          }
        }
      }
    });

  const [, setIsFormValid] = useState(false);
  //const [, setOpen] = React.useState(false);
  const [, setIsLoading] = useState(true);
  const [updateDom, setUpdateDom] = useState(false);

  const secretKey = "mySecretKey"; // Chave secreta para criptografia

  useEffect(() => {
    init();
    setTimeout(() => {
      setCarregando(false);
    }, 2000);
  }, [])

  async function init() {

    const [turmaCadeirasRequest, cursoRequest, turmaRequest, anoRequest] = await Promise.all([requesicoes().getTurmasCadeiras(), requesicoes().buscarCurso('?campos=nome,id'), requesicoes().getTurmas(), requesicoes().buscarAnosFreque()]);//'?campos=nome,id,apelido,sexo,numero,curso.nome,curriculo.ano,matricula'
    cursos = cursoRequest.data;
    turmas = turmaRequest.data;
    anos = anoRequest.data
    turmasCadeira = turmaCadeirasRequest.data.map((x, i) => ({
      ...x, NO: i + 1, docente: x.docente.nome + ' ' + x.docente.apelido, curso: x.curso.nome, turma: x.turma.turma, disciplina: x.cadeira.nome
      //turma:turmas.map(t=>{return t.turma})

    }));
    // console.log(turmasCadeira,'Turmas cadeiras')

    if (turmasCadeira.length === 0) {
      turmasCadeira = turmas.map((t, i) => ({ ...t, NO: i + 1, curso: t.curso.nome }))
    }

    setUpdateDom(!updateDom);

  }
  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  const handleEdit = (id) => {
    //console.log(id,'Id')
    history.push(`/class/class_edit/${id}`);
  };

  const handleRowDoubleClick = (rowData, rowMeta) => {
    const dataIndex = rowMeta.rowIndex;
    const selectedRow = turmasCadeira[dataIndex];
    setSelectedData(selectedRow);
    const id = selectedRow.id;

    history.push(`/class/detalhes/${id}`);

    //history.push(`/student/detalhes/${encryptedId}`);
  };
  const validationSchema = Yup.object().shape({
    turma: Yup.string().required("Campo Obrigatorio"),
  })


  const handleCellClick = (rowIndex, columnIndex) => {

    const currentTime = new Date().getTime();
    const timeDiff = currentTime - lastClickTime;

    // Verifique se o tempo entre os cliques é menor que 300ms
    if (timeDiff < 300) {
      handleRowDoubleClick(rowIndex, columnIndex);
    } else {
      setLastClickTime(currentTime);
    }
  };

  return (
    <div>
      {carregando ? (
        <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', width: '100%', marginTop: '20%' }}>
          <MoonLoader color="#36d7b7" />
        </div>

      ) : (
        <div>
          <MuiThemeProvider theme={getMuiTheme()}>
            <MUIDataTable
              title={'Lista das Turmas'}
              columns={columns(handleEdit)}
              data={turmasCadeira}
              options={{
                onRowClick: handleCellClick
              }}
            />
            <Fab variant="extended" size="medium" style={{
              position: 'fixed',
              bottom: '60px', zIndex: 500,
              right: '15px'
            }} color="primary" onClick={handleClickOpen}>Adicionar</Fab>
          </MuiThemeProvider>

          <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
            <DialogTitle id="form-dialog-title">Formar Turma</DialogTitle>
            <DialogContent>
              <DialogContentText>
                Preencha os dados da Turma
              </DialogContentText>
              <Formik initialValues={{}} validationSchema={validationSchema}

                onSubmit={(values, { setSubmitting }) => {
                  async function save(values) {
                    // values.semestres=anoFrequencia
                    try {

                      await Promise.resolve(requesicoes().postTurmas([values]));
                      //setOpen(true)
                      Swal.fire(
                        'Exito!',
                        'Cadeira cadastrada com sucesso!',
                        'success'
                      )
                      init();

                    } catch (error) {
                      // console.log(error.response.data)
                      Swal.fire({
                        icon: 'error',
                        title: 'Oops...',
                        text: error.response.data.message,
                        footer: 'Se o erro pressistir porfavor contacte o centro Informatico'
                      })

                    }
                  }
                  save(values);
                  setTimeout(() => {
                    setSubmitting(false);
                  }, 500);
                }}>
                {(props) => {
                  const { isSubmitting, errors, touched, handleChange, handleBlur, setFieldValue, values } = props;
                  return (
                    <Form>
                      <Grid container spacing={2}>
                        <Grid item xs={12} sm={6}>
                          <TextField
                            type="text" variant="outlined" name="turma"
                            error={(errors.turma && touched.turma)}
                            helperText={errors.turma} onChange={handleChange} onBlur={handleBlur}
                            value={values.turma} margin="dense"
                            id="turma" fullWidth label="Designação da Turma" />

                        </Grid>
                        <Grid item xs={12} sm={6}>
                          <TextField
                            type="text" variant="outlined" name="sala"
                            error={(errors.sala && touched.sala)}
                            helperText={errors.sala} onChange={handleChange} onBlur={handleBlur}
                            value={values.sala} margin="dense"
                            id="sala" fullWidth label="Numero da sala" />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                          <TextField
                            type="text" variant="outlined" name="qtd"
                            error={(errors.qtd && touched.qtd)}
                            helperText={errors.qtd} onChange={handleChange} onBlur={handleBlur}
                            value={values.qtd} margin="dense"
                            id="qtd" fullWidth label="Numero Maximo de Estudante" />
                        </Grid>

                        <Grid item xs={12} sm={6} >
                          <Autocomplete onChange={(e, v) => { setFieldValue('curso', v); }}
                            fullWidth select name="curso"
                            getOptionLabel={option => option.nome}
                            options={cursos} style={{ minWidth: 200 }}
                            renderInput={(params) => <TextField variant="outlined" margin="dense" label="Curso"
                              fullWidth {...params} placeholder="Seleccione uma opção" />}
                          />

                        </Grid>
                        <Grid item sm={6}>
                          <TextField
                            type="text" variant="outlined" name="ano"
                            error={(errors.ano && touched.ano)}
                            helperText={errors.ano}
                            onChange={handleChange}
                            onBlur={handleBlur} select
                            value={values.ano} margin="dense"
                            id="ano" fullWidth label="Ano">
                            {anos.map(opt => <MenuItem value={opt.values} key={opt}>{opt.label}</MenuItem>)}
                          </TextField>

                        </Grid>

                      </Grid>
                      <DialogActions>
                        <Button onClick={handleClose}>Cancelar</Button>
                        <Button type="submit" onClick={handleClose}>Salvar</Button>
                      </DialogActions>
                    </Form>)
                }}
              </Formik>
            </DialogContent>


          </Dialog>


        </div>
      )}
    </div>
  );
}
